<template>
  <div>
    <h2 class="adminheader">Admin</h2>
    <br>
    <!-- Get Radio Data -->
    <div>
      <h3>Get Radio Data</h3>
      <button v-on:click="showradiodata=true">Get Radio Data</button>
      <div v-if="showradiodata===true">{{radiochannels}}</div>
    </div>
    <br>
    <br>
    <!-- Create New Radio Station -->
    <div>
      <h3>Create New Radio Station</h3>
      <form v-on:submit="newradiostation">
        <input type="text" name="radiostation" placeholder="New Radio Station" /><br>
        <input type="text" name="stationurl" placeholder="enter radio URL" /><br>
        <input type="text" name="nowplayingurl" placeholder="enter now playing URL" /><br>
        <input type="text" name="stationcountry" placeholder="enter radio country" /><br>
        <input type="text" name="stationlanguage" placeholder="enter radio language" /><br>
        <input type="submit" value="Create Radio Station" />
      </form>
    </div>
    <br>
    <br>
    <!-- Add Secured Content -->
    <div>
      <h3>Add Secured Content</h3>
      <br>
      <button v-on:click="selectChannelForSecuredContent">Select Channel</button>
      <form class="profileform">
        <select v-model="selectedchannel">
          <option v-for="radiochannelname in radiochannelnames">{{radiochannelname}}</option>
        </select>
      </form>
      <form>
        <input type="text" name="securedcontent" placeholder="enter secured content" v-model="securecontent" />
        <br>
      </form>
      <button v-on:click="submitSecuredContent">Submit Secure Content</button>
    </div>
    <br>
    <br>
    <!-- Delete Channel -->
    <div>
      <h3>Delete Channel</h3>
      <br>
      <form v-on:submit="deletechannel">
        <!--<input v-bind:href="selectedradio" type="text" name="channel" placeholder="channel" /><br>-->
        <input type="text" name="deleteChannel" placeholder="delete channel" /><br>
        <input type="submit" value="Delete Channel" />
      </form>
    </div>
    <br>
    <br>
    <!-- Delete User -->
    <div>
      <h3>Delete User</h3>
      <br>
      <form v-on:submit="deleteuser">
        <input type="text" name="deleteUser" placeholder="delete user" /><br>
        <input type="submit" value="Delete User" />
      </form>
    </div>
    <br>
    <br>
    <!-- Display all Users -->
    <div>
      <h3>Display all Users</h3>
      <button v-on:click="getUserData">Get all Users</button>
      <div> {{ userdata }} </div>
    </div>
  </div>
</template>

<script>
import router from '../router'
import axios from 'axios'
export default {
  name: 'Admin',
  data () {
    return {
      user: {
        id: '123',
        username: 'not found',
        email: 'not found',
        password: 'not found',
        mode: 'not found',
        channel: 'not found',
        channel: {radioname: 'please select', radiourl: 'blabla'},
        alternativechannel: 'not found',
        alternativechannel: {radioname: 'please select', radiourl: 'blabla'},
        shmoo: ['lala', 'gugu']
      },
      userdata: ['bla', 'blabla'],
      radiochannels: [],
      radiochannelnames: [],
      selectedchannel: '',
      securecontent: '',
      showradiodata: false
    }
  },
  methods: {
    // greift beim laden der seite die verfügbaren channels ab
    getRadioData: function () {
      let that = this
      axios.get('/api/radiodata')
        .then((response) => {
          that.radiochannels = response.data.radiodata
        })
        .catch((errors) => {
          console.log(errors)
          router.push('/login')
        })
    },
    // neuen sender erstellen
    newradiostation: (e) => {
      e.preventDefault()
      let radiostation = e.target.elements.radiostation.value
      let stationurl = e.target.elements.stationurl.value
      let nowplayingurl = e.target.elements.nowplayingurl.value
      let stationcountry = e.target.elements.stationcountry.value
      let stationlanguage = e.target.elements.stationlanguage.value
      let submitnewradiostation = () => {
        let data = {
          radiostation: radiostation,
          stationurl: stationurl,
          nowplayingurl: nowplayingurl,
          stationcountry: stationcountry,
          stationlanguage: stationlanguage
        }
        console.log(data)
        axios.post('/api/newradio', data)
          .then((response) => {
            console.log('new radio station created')
          })
          .catch((errors) => {
            console.log('Cannot create new radio station')
          })
      }
      submitnewradiostation()
    },
    /*
    addsecuredcontent: (e) => {
      var that = this
      console.log(this.selectedchannel)
      e.preventDefault()
      let securedContent = e.target.elements.securedcontent.value
      console.log(that.selectedchannel)
      console.log('inside addsecurecontent frontend')
      let submitsecuredcontent = () => {
        console.log(securedContent)
        console.log(that.selectedchannel)
        var channell = that.selectedchannel
        let data = {
          securedContent: securedContent,
          channell: channell
        }
        console.log(data)
        axios.post("/api/securecontent", data)
          .then((response) => {
            console.log("new securecontent created")
          })
          .catch((errors) => {
            console.log("Cannot create new securecontent")
          })
      }
      submitsecuredcontent()
    },
    */
    // secured content hinzufügen - in zwei schritten weil man erst den sender auswählt und dann diesem sender den secured content hinzufügt
    selectChannelForSecuredContent: function () {
      var that = this
      var i
      for (i = 0; i < this.radiochannels.length; i++) {
        // var thisradiochannel = this.radiochannels[i]
        that.radiochannelnames.push(this.radiochannels[i].radioname)
      }
      console.log('halilo')
    },
    submitSecuredContent: function () {
      let securedContent = this.securecontent
      let channell = this.selectedchannel
      console.log('inside addsecurecontent frontend')
      let submitsecuredcontent = () => {
        let data = {
          securedContent: securedContent,
          channell: channell
        }
        // console.log(data)
        axios.post('/api/securecontent', data)
          .then((response) => {
            console.log('new securecontent created')
          })
          .catch((errors) => {
            console.log('Cannot create new securecontent')
          })
      }
      submitsecuredcontent()
    },
    // sender löschen
    deletechannel: (e) => {
      e.preventDefault()
      let deletedChannel = e.target.elements.deleteChannel.value
      console.log('inside deletechannel frontend')
      let deletEChannel = () => {
        let data = {
          deletedChannel: deletedChannel
        }
        // console.log(data)
        axios.post('/api/deletechannel', data)
          .then((response) => {
            console.log('channel deleted')
          })
          .catch((errors) => {
            console.log('Cannot delete channel')
          })
      }
      deletEChannel()
    },
    // user löschen
    deleteuser: (e) => {
      e.preventDefault()
      let deletedUser = e.target.elements.deleteUser.value
      console.log('inside deleteuser frontend')
      let deletEUser = () => {
        let data = {
          deletedUser: deletedUser
        }
        // console.log(data)
        axios.post('/api/deleteuser', data)
          .then((response) => {
            console.log('user deleted')
          })
          .catch((errors) => {
            console.log('Cannot delete user')
          })
      }
      deletEUser()
    },
    // userdaten abrufen
    getUserData: function () {
      let self = this
      let that = this
      console.log('hallo')
      axios.get('/api/userdata')
        .then((response) => {
          // console.log(response)
          self.$set(this, 'userdata', response.data.allusers)
          that.userdata = response.data.allusers
        })
        .catch((errors) => {
          console.log(errors)
          router.push('/login')
        })
    }
  },
  mounted () {
    this.getRadioData()
  }
}
</script>
<style>
  .adminheader {
    margin-top: 20px;
  }
</style>



